*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

body{
  background-image:linear-gradient(to top, #281736, #621878);
  font-family: 'Poppins', sans-serif;
  height: 100vh;
}

header{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}
header>h1{
  color: white;
  font-weight: 500  ;
}
form{
  margin-top: 10px;
  background-color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 7px;
}

#input-box{
  width: 200px;
  height: 40px;
  padding: 10px;
  outline: none;
  border: none;
  border-radius: 15px;
  font-size: 16px;
  font-weight: 400;
  font-family: 'Poppins', sans-serif;
  margin: 10px;
}
#addBtn{
  width: 100px;
  height: 40px;
  border: none;
  border-radius: 15px;
  padding: 8px;
  background-color: #ce18ec;
  color: white;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}
#task-list{
  display: flex;
  list-style: none;
  padding: 50px;
}

#task-list li{
  background-color: #24273d;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 300px;
  margin: 10px;
  padding: 30px;
  text-align: center;
  border-radius: 15px;
  align-items: center;
  color: #ffff;
}
.btn-container{
  display: flex;
  flex-direction: column;
}

.btn-container button{
  font-family: 'Poppins', sans-serif;
  border: none;
  border-radius: 10px;
  margin: 4px;
  width:200px;
  height: 40px;
  cursor: pointer;
}

.btn-container .dltBtn { 
  background-color: red;
  color: white;
}
.btn-container .editBtn{
  background-color: #3425ac;
  color: white;
}

/* Modal */
.modal {
  display:block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 60px;
  text-align: center;
}

.modal-content {
  background-color: #9564f3;
  margin: 10% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 500px;
  border-radius: 5px;
}
#edit-input{
  width:100%;
  padding: 13px;
  font-family: 'Poppins', sans-serif;
  border-radius: 5px;
  border: none;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
}

/* Modal buttons */
.modal-buttons {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
  gap: 5px;

}
#update{
  width: 50%;
  border: none;
  background-color: #2f047f;
  border-radius: 5px;
  color: white;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}

.close-button {
  width: 50%;
  padding: 8px 12px;
  background-color: #2f047f;
  border-radius: 5px;
  color: white;
  font-family: 'Poppins', sans-serif;
  border: none;
  cursor: pointer;
}


